  padding-bottom: 118px;
  color: #ffffff;
  background-color: #316dc2;
}
@media (max-width: 1199px) {
  .contacts {
    padding-top: 96px;
    padding-bottom: 99px;
  }
}
@media (max-width: 767px) {
  .contacts {
    padding-top: 65px;
    padding-bottom: 67px;
  }
}

.contacts__text {
  position: relative;
  display: grid;
  grid-template-columns: min-content 48.6%;
  justify-content: space-between;
  padding-bottom: 33px;
}
.contacts__text::after {
  position: absolute;
  content: "";
  bottom: 0;
  width: 100%;
  height: 2px;
  background-color: rgba(255, 255, 255, 0.2);
}
@media (max-width: 1199px) {
  .contacts__text {
    display: flex;
    flex-direction: column;
    justify-content: unset;
    row-gap: 28px;
    padding-bottom: 26px;
  }
}
@media (max-width: 767px) {
  .contacts__text {
    row-gap: 29px;
  }
}

.contacts__title {
  padding-top: 5px;
}

.contacts__list {
  margin: 0;
  padding: 0;
  display: flex;
  grid-column: 2/3;
  gap: 50px;
  list-style-type: none;
}
@media (max-width: 1199px) {
  .contacts__list {
    display: grid;
    grid-template-columns: minmax(auto, 40%) auto auto;
    justify-content: space-between;
  }
}
@media (max-width: 767px) {
  .contacts__list {
    display: flex;
    flex-direction: column;
    gap: 13px;
  }
}

.contacts__item {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.contacts__item h3 {
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 12px;
  letter-spacing: 0.12px;
  text-transform: uppercase;
  opacity: 0.8;
}
.contacts__item address,
.contacts__item a {
  font-size: 16px;
  font-style: normal;
  line-height: 26px;
  color: #ffffff;
}
@media (max-width: 1199px) {
  .contacts__item address,
.contacts__item a {
    line-height: 21px;
  }
}
@media (max-width: 767px) {
  .contacts__item address,
.contacts__item a {
    font-size: 14px;
    line-height: 20px;
  }
}
@media (max-width: 1199px) {
  .contacts__item {
    gap: 18px;
  }
}
@media (max-width: 767px) {
  .contacts__item {
    gap: 9px;
  }
}
.contacts__item a:hover {
  text-decoration: underline;
  text-underline-offset: 5px;
}
.contacts__item a:active {
  text-decoration: none;
  opacity: 0.8;
}
.contacts__item a:focus, .contacts__item a:focus-visible {
  text-decoration: underline;
  text-underline-offset: 5px;
  outline: none;
}

.contacts__map {
  height: 500px;
  width: 100%;
  margin-top: 40px;
  border-radius: 16px;
}
@media (max-width: 1199px) {
  .contacts__map {
    margin-top: 30px;
  }
}
@media (max-width: 767px) {
  .contacts__map {
    height: 240px;
  }
}

