.write-to-us__checkbox label {
  display: grid;
  grid-template-columns: 20px auto;
  gap: 10px;
  align-items: center;
  cursor: pointer;
}
.write-to-us__checkbox:hover .write-to-us__checkbox-icon {
  border: 1px solid #ffffff;
}
.write-to-us__checkbox:hover input[type=checkbox]:checked ~ .write-to-us__checkbox-icon {
  background-color: transparent;
  border: 1px solid #ffffff;
}
.write-to-us__checkbox:hover input[type=checkbox]:checked ~ .write-to-us__checkbox-icon::after {
  background-color: #ffffff;
}
.write-to-us__checkbox:focus .write-to-us__checkbox-icon, .write-to-us__checkbox:focus-visible .write-to-us__checkbox-icon {
  border: 1px solid #ffffff;
}
.write-to-us__checkbox input[type=checkbox] {
  display: none;
}
.write-to-us__checkbox span:not(.write-to-us__checkbox-icon) {
  font-size: 12px;
  line-height: 12px;
  opacity: 0.9;
}
.write-to-us__checkbox.is-invalid .write-to-us__checkbox-icon {
  border: 1px solid #ff5e66;
  opacity: 0.7;
  background: rgba(255, 94, 102, 0.2);
}

.write-to-us__checkbox-icon {
  position: relative;
  display: block;
  width: 20px;
  height: 20px;
  border: 1px solid rgba(255, 255, 255, 0.7);
  background-color: transparent;
  border-radius: 2px;
  cursor: pointer;
}
.write-to-us__checkbox-icon::after {
  position: absolute;
  inset: 0;
  content: "";
  margin: auto;
  width: 8px;
  height: 6px;
  mask-image: url("../img/svg/checkbox.svg");
  mask-repeat: no-repeat;
  mask-size: cover;
  background-color: transparent;
}
.write-to-us__checkbox-icon:hover {
  border: 1px solid #ffffff;
}
.write-to-us__checkbox-icon:focus, .write-to-us__checkbox-icon:focus-visible {
  border: 1px solid #ffffff;
}
.write-to-us__checkbox-icon:disabled {
  border: 1px solid #ffffff;
  background-color: rgba(255, 255, 255, 0.6);
  pointer-events: none;
}

input[type=checkbox]:checked ~ .write-to-us__checkbox-icon {
  background-color: #ffffff;
}
input[type=checkbox]:checked ~ .write-to-us__checkbox-icon::after {
  background-color: #316dc2;
}
input[type=checkbox]:checked ~ .write-to-us__checkbox-icon:hover {
  background-color: transparent;
  border: 1px solid #ffffff;
}
input[type=checkbox]:checked ~ .write-to-us__checkbox-icon:hover::after {
  background-color: #ffffff;
}
input[type=checkbox]:checked ~ .write-to-us__checkbox-icon:focus, input[type=checkbox]:checked ~ .write-to-us__checkbox-icon:focus-visible {
  background-color: transparent;
  border: 1px solid #ffffff;
}
input[type=checkbox]:checked ~ .write-to-us__checkbox-icon:focus::after, input[type=checkbox]:checked ~ .write-to-us__checkbox-icon:focus-visible::after {
  background-color: #ffffff;
}
input[type=checkbox]:checked ~ .write-to-us__checkbox-icon:disabled {
  background-color: #a9afb7;
  pointer-events: none;
}
input[type=checkbox]:checked ~ .write-to-us__checkbox-icon:disabled::after {
  background-color: #ffffff;
}

